import {
  useParams,
  useLocation,
  useNavigate,
  Outlet,
  Link,
} from 'react-router-dom';
import css from './MovieDetails.module.css';
import { fetchMovieId } from 'utils/FetchFunc';
import { useState, useEffect } from 'react';
import { ProgressBar } from 'react-loader-spinner';
import Notiflix from 'notiflix';
const MovieDetails = () => {
  const [movieDetails, setMovieDetails] = useState([]);
  const { movieId } = useParams();
  const [movieIdState] = useState(movieId);
  const [isLoading, setIsLoading] = useState(true);
  const location = useLocation();
  const navigate = useNavigate();
  useEffect(() => {
    const fetchMovieDetails = async () => {
      try {
        const movie = await fetchMovieId(movieIdState);
        setMovieDetails(movie.data);
      } catch (error) {
        console.log(error);
        Notiflix.Notify.failure('Something went wrong :( Try again later...');
        navigate(location.state.from);
      } finally {
        setIsLoading(false);
      }
    };
    fetchMovieDetails();
  }, [location.state.from, movieIdState, navigate]);
  const genresString = genres => {
    if (genres !== undefined) {
      let newArray = [];
      genres.forEach(element => {
        newArray.push(element.name);
      });
      return newArray.join(', ');
    }
  };
  const handleBackButton = () => {
    navigate(location.state.from);
  };
  return (
    <>
      {isLoading === true && <ProgressBar width="100%" />}
      {isLoading === false && (
        <>
          <button
            className={css.button}
            type="button"
            onClick={handleBackButton}
          >
            Go back
          </button>
          <div className={css.movie}>
            <img
              loading="lazy"
              src={
                movieDetails.poster_path !== null
                  ? `https://image.tmdb.org/t/p/w500${movieDetails.poster_path}`
                  : 'data:image/png;base64,'
              }
              alt={
                movieDetails.title === undefined
                  ? movieDetails.name
                  : movieDetails.title
              }
            />
            <div>
              <h2>
                {movieDetails.title === undefined
                  ? movieDetails.name
                  : movieDetails.title}{' '}
                {movieDetails.release_date !== '' &&
                  `(${movieDetails.release_date})`}
              </h2>
              <p>{`User score: ${Math.round(
                movieDetails.vote_average * 10
              )}%`}</p>
              <h3>Overview</h3>
              <p>{movieDetails.overview}</p>
              <h4>Genres</h4>
              <p>{genresString(movieDetails.genres)}</p>
            </div>
          </div>
          <div className={css.additionalInfo}>
            <p className={css.paragraph}>Additional information</p>
            <ul className={css.list}>
              <li>
                <Link
                  className={css.listItem}
                  to="cast"
                  state={{ from: location.state.from }}
                >
                  Cast
                </Link>
              </li>
              <li>
                <Link
                  className={css.listItem}
                  to="reviews"
                  state={{ from: location.state.from }}
                >
                  Reviews
                </Link>
              </li>
            </ul>
          </div>
          <Outlet />
        </>
      )}
    </>
  );
};
export default MovieDetails;
